:root {
  --main-color: #fcfcfc; /* Определяем переменную с цветом */
  --secondary-color: rgb(9, 47, 73);
}

.stand {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0059b3;
  padding: 96px;
  border-radius: 30px;
}

.inputs {
  width: 372px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  gap: 48px;
  margin: 0 20px;
  padding: 16px;
  background-color: var(--main-color);
  border-radius: 10px;
}
.inputs:before {
  content: 'SuperInpetText';
}

.buttons {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin: 0 20px;
  padding: 16px;
  background-color: var(--main-color);
  border-radius: 10px;
}
.buttons:before {
  content: 'SuperButtons';
}

.checkboxes {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  gap: 36px;
  margin: 0 20px;
  padding: 16px;
  background-color: var(--main-color);
  border-radius: 10px;
}
.checkboxes:before {
  content: 'SuperCheckboxes';
}
